import Axios from 'axios'

const ajax = (data: any = {}, { url, method }:any, showErrMsg = true) => {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      method,
      params: data.query || (method === 'get' ? data : {}),
      data: data.body || (method === 'post' ? data : {}),
      timeout: 1000 * 60,
      headers: {
        ...data.headers
      }
    }).then(res => {
      if (Number(res.data.code) !== 200) {
        // 业务异常
        // 请在此处处理业务异常
        if (showErrMsg) console.log(res.data.message)
        reject(res.data)
        return false
      }
      resolve(res.data)
    }).catch(err => {
      // http异常
      // 请在此处处理http异常
      reject(err.data)
      if (err.message === undefined) return false
      if (err.toString().indexOf('401') !== -1) {
        console.log('登录失效，请重新登录。') // 登录失效，请重新登录。
        return false
      }
      if (showErrMsg) console.log('网络异常，请稍后再试!') // 网络异常，请稍后再试!
    })
  })
}

export default ajax
