<template>
  <div class="g-login-box">
    <van-nav-bar
      title="登录"
      left-arrow
      @click-left="toPageReturn"
    />
    <img class="u-bg" src="./../assets/logo.png">
    <van-form>
      <van-field
        v-model="state.user"
        name="联系电话"
        label="联系电话"
        placeholder="联系电话"
        :rules="[{ required: false, message: '请填写联系电话' }]"
      />
      <van-field
        name="验证码"
        label="验证码"
        placeholder="验证码"
        :rules="[{ required: false, message: '请填写验证码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit" @click="login">
          登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'
import router from './../router'
import api from '../api'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false,
      user: ''
    })
    const toPage = () => {
      if (state.user.includes('2')) {
        router.push('/hello-world?type=2')
      } else {
        router.push('/hello-world?type=1')
      }
    }
    const login = () => {
      api.auth.postAuthLogin({}).then(res => {
        toPage()
      }).finally(() => {
        toPage()
      })
    }
    const toPageReturn = () => router.go(-1)
    return {
      state,
      toPage,
      toPageReturn,
      login
    }
  }
})
</script>

<style scoped>
.u-bg {
  width: 100%;
  height: 100px;
}
</style>
